.heading{
    color:white;
    padding-top:100px;
}
.list{
    list-style-type: none;
}
li{
    color: white;
}
.wholeproject{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.projectheading{
    
    font-size: xx-large;
}
.padd{
    width:fit-content;
    height: fit-content;
    padding-top: 10px;
    padding-left:10px;
    padding-right:10px;
    padding-bottom:10px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 40px;
    margin-bottom: 40px;
}
.allprojectblocks{ 
    color:white;
    display: grid;
    grid-gap:20px;
    grid-template-columns: repeat(auto-fill,minmax(400px,1fr)); 
}
.projectblock{
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    cursor: pointer;
    display:inline-block;
    text-align: center;
    border: 1px solid transparent;
    /*backdrop-filter: blur(2px);*/
    border-radius: 5px;
    transition: all 0.3% linear;
    align-items: start;
    padding: 5px;
    margin:5px;
}
.groww { 
    transition: all 0.9 ease-in-out; 
    }
    
.groww:hover { 
    transform: scale(1.1); 
    }