.card{
    width:100%;
    margin: auto;
    margin-top: 120px;
    padding-bottom:10%;
}
.info{
    color:whitesmoke;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    size:50%;
}
.social{
    font-size:150%;
    padding-left: 5px;
    padding-right: 5px;
    padding-top: 70px;
    transition-duration: 500ms;

}
.borrad{
    
    border-radius: 3000px;
    width: 300px;
    height: 300px;
    margin:30px 30px 30px 30px;
    transition-duration: 1000ms;
}
.borrad:hover{
    
    width: 330px;
    height:330px;
    margin: 15px;
    transition-duration: 1000ms;
}
.groww { 
    transition: all .2s ease-in-out; 
    }
    
.groww:hover { 
    transform: scale(1.1); 
    }