.fontcolor{
    color: whitesmoke;
    
}
.headingcolor{
    color:wheat;

}
.whole{
    padding-left:10%;
    padding-right:10%;
    padding-bottom:10%;
    padding-top: 10%;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.codingid{
    text-decoration: none;
    text-decoration-style: none;
    color:lightgreen;
}
.codingid:hover{
    color: lightskyblue;
}
.spacetop{
    padding-top: 100px;
}
.heading{
    color:white;
}
.allskillblocks{
    display: grid;
    grid-gap:20px;
    grid-template-columns: repeat(auto-fill,minmax(120px,1fr));
}
.skillblock{
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    cursor: pointer;
    display:inline-block;
    text-align: center;
    border: 1px solid transparent;
    /*backdrop-filter: blur(2px);*/
    border-radius: 5px;
    transition: all 0.3% linear;
    align-items: start;
    padding: 5px;
    margin:5px;
}
.alllinksblocks{
    display: grid;
    grid-gap:20px;
    grid-template-columns: repeat(auto-fill,minmax(300px,1fr));
}
.linksblock{
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    cursor: pointer;
    display:inline-block;
    text-align: center;
    border: 1px solid transparent;
    /*backdrop-filter: blur(2px);*/
    border-radius: 5px;
    transition: all 0.3% linear;
    align-items: start;
    padding: 5px;
    margin:5px;
}
.groww { 
    transition: all .2s ease-in-out; 
    }
    
.groww:hover { 
    transform: scale(1.1); 
    }
.intro{
    font-family:sans-serif, 'Arial Narrow Bold', Haettenschweiler,Impact;
}