.bar{
    /*display: flex;*/
    flex-flow: row-reverse;
    align-items:center;
    position: fixed;
    overflow:hidden;
    top:0;
    background: linear-gradient(to left,rgb(59, 58, 58) 0%, rgb(0, 0, 0) 100%);
    opacity: 1;
    width: 100%;
}
.bar1{
    flex-flow:row-reverse;
