.but{
    text-decoration: none;
    color: white;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 100%;
    padding:1%;
    background: linear-gradient(to left,rgb(20, 20, 20) 0%, rgb(53, 53, 53) 100%);
    
    font-weight: bolder;
}
.but1{
    text-decoration: none;
    color: white;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 60%;
    padding:1%;
    background: linear-gradient(to left,rgb(20, 20, 20) 0%, rgb(53, 53, 53) 100%);
    margin-left: auto;
    font-weight: bolder;
}
div span {
    display: none;
  }
div nav{
    display:flex;
}
@media (max-width: 610px) {
    div nav { display: none; }
    div span { display: flex; }
}
  


